import type {
  StandardConnectFeature,
  StandardConnectMethod,
  StandardDisconnectFeature,
  StandardDisconnectMethod,
  StandardEventsFeature,
  StandardEventsOnMethod,
  SuiFeatures,
  SuiSignAndExecuteTransactionMethod,
  SuiSignPersonalMessageMethod,
  SuiSignTransactionMethod,
  Wallet,
  WalletAccount
} from '@mysten/wallet-standard'
import { getWallets, ReadonlyWalletAccount, SUI_CHAINS } from '@mysten/wallet-standard'

const WALLET_NAME = 'Phantom'

export default function registerPhantomWallet() {
  const walletsApi = getWallets()
  const registeredWallets = walletsApi.get()

  if (registeredWallets.find(wallet => wallet.name === WALLET_NAME && wallet.chains.includes('sui:mainnet'))) {
    // console.log(
    //   'registeredWallets wallet123: ',
    //   registeredWallets.find(wallet => wallet.name === WALLET_NAME)
    // )
    // console.warn('registerPhantomWallet: Unsafe Burner Wallet already registered, skipping duplicate registration.')
    return
  }

  return walletsApi.register(new PhantomWallet())
}

class PhantomWallet implements Wallet {
  #accounts: WalletAccount[] | null = null
  #connecting: boolean
  #connected: boolean

  constructor() {
    this.#connecting = false
    this.#connected = false
  }

  get version() {
    return '1.0.0' as const
  }

  get name() {
    return WALLET_NAME
  }

  get icon() {
    return 'data:image/png;base64,' as const
  }

  // Return the Sui chains that your wallet supports.
  get chains() {
    return SUI_CHAINS
  }

  get accounts() {
    if (this.#connected && this.#accounts) {
      return this.#accounts
    } else {
      return []
    }
  }

  get features(): StandardConnectFeature & StandardEventsFeature & SuiFeatures & StandardDisconnectFeature {
    return {
      'standard:connect': {
        version: '1.0.0',
        connect: this.#connect
      },
      'standard:disconnect': {
        version: '1.0.0',
        disconnect: this.#disconnect
      },
      'standard:events': {
        version: '1.0.0',
        on: this.#on
      },
      'sui:signPersonalMessage': {
        version: '1.1.0',
        signPersonalMessage: this.#signPersonalMessage
      },
      'sui:signTransaction': {
        version: '2.0.0',
        signTransaction: this.#signTransaction
      },
      'sui:signAndExecuteTransaction': {
        version: '2.0.0',
        signAndExecuteTransaction: this.#signAndExecuteTransaction
      }
    }
  }

  #on: StandardEventsOnMethod = () => {
    return () => {}
  }

  #disconnect: StandardDisconnectMethod = async () => {
    // TODO: 需要删除localStorage中的地址
    // localStorage.removeItem(STASHED_RECENT_ADDRESS_KEY)
    console.log('phantomWallet disconnect##')
    const provider = (window as any).embed_phantom
    await provider?.disconnect()
    this.#accounts = []
    this.#connected = false
  }

  #connect: StandardConnectMethod = async () => {
    if (this.#connecting) {
      throw new Error('Already connecting')
    }

    this.#connecting = true
    this.#connected = false

    try {
      const provider = (window as any).embed_phantom
      console.log('🚀 --------------------------------------------------------------------------🚀')
      console.log('🚀 ~ PhantomWallet ~ #connect:StandardConnectMethod= ~ provider:', provider)
      console.log('🚀 --------------------------------------------------------------------------🚀')

      const accountsui = await provider.sui.requestAccount()

      const account = new ReadonlyWalletAccount({
        address: accountsui.address,
        publicKey: accountsui.publicKey,
        chains: ['sui:mainnet'],
        features: ['sui:signAndExecuteTransactionBlock', 'sui:signTransactionBlock', 'sui:signTransaction', 'sui:signAndExecuteTransaction']
      })
      console.log('🚀 ------------------------------------------------------------------------🚀')
      console.log('🚀 ~ PhantomWallet ~ #connect:StandardConnectMethod= ~ account:', account)
      console.log('🚀 ------------------------------------------------------------------------🚀')

      this.#accounts = [account]
      this.#connecting = false
      this.#connected = true

      return {
        accounts: this.accounts
      }
    } catch (e) {
      console.log('🚀 ------------------------------------------------------------🚀')
      console.log('🚀 ~ PhantomWallet ~ #connect:StandardConnectMethod= ~ e:', e)
      console.log('🚀 ------------------------------------------------------------🚀')

      this.#connecting = false
      this.#connected = false
      throw e
    }
  }

  #signPersonalMessage: SuiSignPersonalMessageMethod = async messageInput => {
    const provider = (window as any).embed_phantom
    console.log('phantom signPersonalMessage messageInput###', messageInput)
    try {
      const { bytes, signature } = await provider.sui.signMessage(messageInput.message, this.#accounts![0].address)
      return { bytes, signature }
    } catch (error) {
      throw new Error(error instanceof Error ? error.message : 'Failed to sign message')
    }
  }

  #signTransaction: SuiSignTransactionMethod = async transactionInput => {
    console.log('🚀 ~ PhantomWallet ~ #signTransaction:2= ~ transactionInput:', transactionInput)

    const provider = (window as any).embed_phantom

    const transactionParams = {
      transaction: await transactionInput.transaction.toJSON(),
      address: this.#accounts![0].address,
      networkID: 'sui:mainnet'
    }

    try {
      const { signature, transaction } = await provider.sui.signTransaction(transactionParams)
      console.log('🚀 ~ PhantomWallet ~ #signTransaction:2= ~ res:', {
        signature,
        transaction
      })

      return {
        bytes: transaction,
        signature
      }
    } catch (error) {
      console.log('🚀 ~ PhantomWallet ~ #signTransaction:2= ~ error:', error)
      throw error
    }
  }

  #signAndExecuteTransaction: SuiSignAndExecuteTransactionMethod = async transactionInput => {
    console.log('🚀 ~ PhantomWallet ~ #signAndExecuteTransaction:4= ~ transactionInput:', transactionInput)

    const provider = (window as any).embed_phantom

    const transactionParams = {
      transaction: await transactionInput.transaction.toJSON(),
      address: this.#accounts![0].address,
      networkID: 'sui:mainnet'
    }

    try {
      const res = await provider.sui.signAndSendTransaction(transactionParams)
      console.log('🚀 ~ PhantomWallet ~ #signTransaction:2= ~ res:', res)
      return res
    } catch (error) {
      console.log('🚀 ~ PhantomWallet ~ #signAndExecuteTransaction:2= ~ error:', error)
      throw error
    }
  }
}
