import { CommonTypeInfo, TransactionStatusType } from '@cetus/types/src/common-types'
import { Box, Image, Spinner, Text, VStack } from '@chakra-ui/react'

type ModalDescriptionProps = {
  status?: TransactionStatusType
  info?: CommonTypeInfo | undefined
}

export function ModalDescription(props: ModalDescriptionProps) {
  const { status = 'confirmation', info } = props

  const description = info?.modalDescriptionText
  const iconUrl = info?.iconUrl

  // 待确认 / 已提交
  if (status === 'confirmation' || status === 'submitted') {
    return (
      <>
        <VStack gap="20px" alignItems="center">
          {iconUrl && <Image src={iconUrl} w="100px" h="100px" alt="icon" borderRadius="8px" objectFit="contain" />}
          {!iconUrl && <Spinner mt="15px" mb="15px" size="xl" color="primary" thickness="4px" />}
          <Text color="text_caption" lineHeight="20px" textAlign="center">
            {description}
          </Text>
        </VStack>
      </>
    )
  }

  // 失败
  if (status === 'rejected') {
    return (
      <VStack gap="8px" alignItems="center">
        {iconUrl && <Image src={iconUrl} w="100px" h="100px" alt="icon" borderRadius="8px" objectFit="contain" />}
        {!iconUrl && <Image src="/images/img_transactionrejected@2x.png" w="150px" h="150px" alt="icon" />}
        <Box h="12px" />
        <Text color="text_caption" fontSize="16px" textAlign="center">
          Transaction failed
        </Text>
        <Text color="primary_red" lineHeight="20px" textAlign="center">
          {description}
        </Text>
      </VStack>
    )
  }

  // 成功
  if (status === 'success') {
    return (
      <>
        <VStack gap="8px" alignItems="center">
          {iconUrl && <Image src={iconUrl} w="100px" h="100px" alt="icon" borderRadius="8px" objectFit="contain" />}

          {!iconUrl && <Image src="/images/img_transactionsubmitted@2x.png" w="150px" h="150px" alt="icon" />}
          <Box h="12px" />
          <Text color="text_caption" fontSize="16px">
            Transaction Completed
          </Text>
          <Text color="primary" lineHeight="20px" textAlign="center">
            {description}
          </Text>
        </VStack>
      </>
    )
  }

  return <></>
}
