import { useAccountStore } from '@cetus/stores'
import useCryptoStore from '@cetus/stores/src/crypto'
import useWebConfigStore from '@cetus/stores/src/useWebConfigStore'
import { CheckBox } from '@cetus/ui-kit'
import {
  Box,
  Button,
  Heading,
  HStack,
  Image,
  Modal,
  ModalBody,
  ModalCloseButton,
  ModalContent,
  ModalHeader,
  ModalOverlay,
  Text,
  VStack
} from '@chakra-ui/react'
import { useState } from 'react'

type TermConditionsProps = {
  isOpen: boolean
  onClose: () => void
}
export default function BuyCryptoModal({ isOpen, onClose }: TermConditionsProps) {
  const { isBuyCryptoConfirm, setIsBuyCryptoConfirm } = useWebConfigStore()
  const { setCurrentCrypto, setIsShowAlchemyCrypto, setIsShowKodoCrypto } = useCryptoStore()
  const { currentAccount } = useAccountStore()
  const handleContinue = () => {
    setIsBuyCryptoConfirm(true)
  }
  const onClickItem = async (item: any) => {
    console.log('🚀 ~ onClickItem ~ item:', item)
    setCurrentCrypto(item.id)
    if (item.id === 'alchemy') {
      setIsShowAlchemyCrypto(true)
    } else if (item.id === 'KODO') {
      setIsShowKodoCrypto(true)
    } else {
      if (currentAccount?.address) {
        // const onRampURL = generateOnRampURL({
        //   appId: 'd2baaf93-cda5-404c-9be2-36695a530d02',
        //   destinationWallets: [{ address: currentAccount?.address, blockchains: ['sui'] }]
        // })
        // window.open(
        //   onRampURL,
        //   'Coinbase Pay',
        //   'height=700, width=400, top=0, left=0, toolbar=no, menubar=no, scrollbars=no, resizable=no, location=no, status=no'
        // )

        try {
          const url = 'https://api-sui-cf.cetus.zone/proxy/coin_base/platform/v2/onramp/sessions'
          const res = await fetch(url, {
            // headers: fetchHeaders,
            body: JSON.stringify({
              purchaseCurrency: 'SUI',
              destinationNetwork: 'sui',
              destinationAddress: currentAccount?.address
            }),
            method: 'POST'
          })

          const data = await res.json()
          const onRampURL = data?.session?.onrampUrl

          window.open(
            onRampURL,
            'Coinbase Pay',
            'height=700, width=400, top=0, left=0, toolbar=no, menubar=no, scrollbars=no, resizable=no, location=no, status=no'
          )
        } catch (error) {
          console.log('🚀 ~ onClickItem ~ coinbase error:', error)
        }
      }
    }
    onClose()
  }
  return (
    <Modal autoFocus={false} returnFocusOnClose={false} isOpen={isOpen} onClose={onClose} isCentered>
      <ModalOverlay />
      <ModalContent>
        <ModalHeader>
          <Heading fontWeight="500" fontSize="16px">
            {isBuyCryptoConfirm ? 'Buy Crypto' : ''}
          </Heading>
        </ModalHeader>
        <ModalCloseButton />
        <ModalBody>
          {isBuyCryptoConfirm ? (
            <BuyCryptoSelectCon onClickItem={(item: any) => onClickItem(item)} />
          ) : (
            <BuyCryptoConfirmCon handleContinue={handleContinue} />
          )}
        </ModalBody>
      </ModalContent>
    </Modal>
  )
}

const BuyCryptoConfirmCon = ({ handleContinue }: { handleContinue: () => void }) => {
  const [isChecked, setIsChecked] = useState(false)
  return (
    <Box>
      <Text color="text_caption" lineHeight="20px" mt="16px" mb="24px">
        Please note that the function you are requesting is provided by third-party service providers. If you encounter any issue or need support,
        please contact corresponding third party to resolve it.
      </Text>
      <HStack w="100%" align="center" justify="center">
        <CheckBox
          checked={isChecked}
          onClick={() => {
            setIsChecked(!isChecked)
          }}
        />
        <Text fontSize="14px" lineHeight="1" color="primary">
          I acknowledge the risk
        </Text>
      </HStack>
      <Button
        isDisabled={!isChecked}
        fontWeight="500"
        fontSize="16px"
        mt="30px"
        ml="-17px"
        h="52px"
        w="calc(100% + 34px)"
        mb="-30px"
        onClick={handleContinue}
      >
        Continue
      </Button>
    </Box>
  )
}

const BuyCryptoSelectCon = ({ onClickItem }: { onClickItem: (item: any) => void }) => {
  const list = [
    {
      id: 'alchemy',
      name: 'Alchemy Pay',
      icon: 'alchemypay'
    }
    // {
    //   id: 'coinbase',
    //   name: 'Coinbase Pay',
    //   icon: 'coinbasepay'
    // }
    // {
    //   id: 'KODO',
    //   name: 'Kodo Pay',
    //   icon: 'kodo'
    // }
  ]
  return (
    <VStack gap="0" mt="-12px" mb="8px">
      {list.map((item: any) => {
        return (
          <HStack
            w="100%"
            justify="center"
            borderRadius="12px"
            key={item?.id}
            h="48px"
            cursor="pointer"
            _hover={{
              bg: 'primary_opacity.10',
              p: {
                color: 'primary'
              }
            }}
            onClick={() => onClickItem(item)}
          >
            <Image w="24px" h="24px" src={`/images/${item.icon}.png`} />
            <Text color="text_caption">{item?.name}</Text>
          </HStack>
        )
      })}
    </VStack>
  )
}
