import { CommonTypeInfo, TransactionStatusType } from '@cetus/types/src/common-types'
import { Heading } from '@chakra-ui/react'

type ModalTitleProps = {
  status?: TransactionStatusType
  info?: CommonTypeInfo | undefined
}

export function ModalTitle(props: ModalTitleProps) {
  const { status = 'confirmation', info } = props

  const titleText = info?.modalTitleText

  // 待确认 / 已提交
  if (status === 'confirmation' || status === 'submitted') {
    return (
      <>
        <Heading fontWeight="500" fontSize="16px">
          {titleText || 'Waiting for Confirmation'}
        </Heading>
      </>
    )
  }

  return <></>
}
