import { useAccountStore } from '@cetus/stores'
import useCryptoStore from '@cetus/stores/src/crypto'
import { Icon } from '@cetus/ui-kit'
import { Center, Modal, ModalBody, ModalContent, ModalOverlay, VStack } from '@chakra-ui/react'
import crypto from 'crypto-js'
import { useMemo } from 'react'

type TermConditionsProps = {
  isOpen: boolean
  onClose: () => void
}
export default function BuyCryptoAlchemy({ isOpen, onClose }: TermConditionsProps) {
  const { currentAccount } = useAccountStore()
  const { currentCrypto } = useCryptoStore()

  const encrypt = (plainText: any, secretKeyData: any) => {
    try {
      const key = crypto.enc.Utf8.parse(secretKeyData)
      const iv = crypto.enc.Utf8.parse(secretKeyData.substring(0, 16))
      const encrypted = crypto.AES.encrypt(plainText, key, { iv, mode: crypto.mode.CBC })
      return encrypted.toString()
    } catch (e) {
      console.log(`0613###AES encrypting exception, msg is ${e}`)
    }
    return null
  }
  const iframeUrl = useMemo(() => {
    if (currentAccount?.address && currentCrypto) {
      if (currentCrypto === 'alchemy') {
        const plaintext = `address=${currentAccount?.address}&appId=eux4fx43yH39DsW0`
        const ciphertext = encrypt(plaintext, 'J8Y2oKoFAKiw6OCq')
        const sign = encodeURIComponent(ciphertext)
        return `https://ramp.alchemypay.org/?network=SUI&appId=eux4fx43yH39DsW0&sign=${sign}&address=${currentAccount?.address}`
      }
      return ''
    }
    return ''
  }, [currentCrypto])

  return (
    <Modal autoFocus={false} returnFocusOnClose={false} isOpen={isOpen} onClose={onClose} isCentered>
      <ModalOverlay />
      <ModalContent p="0 !important" bg="transparent !important" borderColor="transparent !important">
        <Center bg="bg_third" position="absolute" right="-14px" top="-14px" w="30px" h="30px" borderRadius="50%" onClick={onClose}>
          <Icon xlinkHref="#icon-icon_close" svgFill="text_caption" />
        </Center>
        <ModalBody p="0 !important" bg="transparent !important" borderColor="transparent !important">
          <VStack>
            {iframeUrl && (
              <iframe
                height="625"
                title="AlchemyPay On/Off Ramp Widget"
                src={iframeUrl}
                frameBorder="0"
                // allowtransparency="false"
                allowFullScreen
                width="100%"
                style={{ display: 'block', maxHeight: '625px', maxWidth: ' 500px' }}
              />
            )}
          </VStack>
        </ModalBody>
      </ModalContent>
    </Modal>
  )
}
