import useExplorer from '@cetus/hooks/src/useExplorer'
import { defaultExplorers, ToastType } from '@cetus/types'
import { Icon } from '@cetus/ui-kit'
import { Button, HStack, Image, Modal, ModalBody, ModalCloseButton, ModalContent, ModalHeader, ModalOverlay, Text, VStack } from '@chakra-ui/react'
import { useMemo } from 'react'
import { ModalDescription } from './ModalDescription'
import { ModalTitle } from './ModalTitle'

type TransactionProps = {
  isOpen: boolean
  onClose: (isManual: boolean) => void
} & ToastType

export default function TransactionModal(props: TransactionProps) {
  const { getExplorerUrl } = useExplorer()

  const { status = 'confirmation', isOpen, onClose, link, tx, getShowInfo } = props

  const showInfo = useMemo(() => {
    if (getShowInfo) {
      return getShowInfo(status)
    }
    return undefined
  }, [status])

  return (
    <Modal autoFocus={false} returnFocusOnClose={false} isOpen={isOpen} onClose={() => onClose(false)} isCentered>
      <ModalOverlay />
      <ModalContent>
        {/* 标题 */}
        <ModalHeader>
          <ModalTitle info={showInfo} status={status} />
        </ModalHeader>
        <ModalCloseButton onClick={() => onClose(true)} />
        <ModalBody>
          <VStack gap="8px" pb="24px" pt="28px" w="100%">
            {/* 描述 */}
            <ModalDescription info={showInfo} status={status} />
            {/* 钱包确认提示 */}
            {status === 'confirmation' && <Text>{showInfo?.modalSubDescriptionText || 'Confirm this transaction in your wallet'}</Text>}
            {/* 钱包确认已确认 */}
            {status === 'submitted' && (
              <Text textAlign="center" color="text_paragraph" fontSize="14px">
                {showInfo?.modalSubDescriptionText || 'Transaction in progress'}
              </Text>
            )}
            {/* 交易成功 */}
            {
              <VStack mt="5px" gap="12px" w="100%">
                {/* 区块游览器 */}
                {link && (
                  <Button
                    mt="12px"
                    h="40px"
                    w="50%"
                    borderRadius="12px"
                    variant="outline"
                    fontSize="14px"
                    borderColor="primary"
                    onClick={() => {
                      onClose(false)
                      window.open(link, '_blank')
                    }}
                  >
                    View on Explorer
                  </Button>
                )}

                {!link && tx && (
                  <VStack gap="12px">
                    <Text color="text_caption" fontSize="sm">
                      View on Explorer
                    </Text>
                    <HStack w="100%" justifyContent="center" gap="16px">
                      {defaultExplorers.map(explorer => {
                        return (
                          <Button
                            key={explorer.name}
                            variant="outline"
                            h="32px"
                            pl="12px"
                            lineHeight="1"
                            color="text_highlight"
                            fontSize="12px"
                            borderColor="button_outline_border"
                            borderRadius="8px"
                            leftIcon={<Image src={explorer.img} alt="SVG Image" boxSize="24px" objectFit="cover" borderRadius="8px" />}
                            rightIcon={<Icon xlinkHref="#icon-icon_link1" fontSize="16px" />}
                            _hover={{
                              borderColor: 'text_highlight',
                              svg: {
                                fill: 'text_caption'
                              }
                            }}
                            onClick={() => {
                              window.open(getExplorerUrl(tx, 'tx', explorer), '_blank')
                            }}
                          >
                            {explorer.name}
                          </Button>
                        )
                      })}
                    </HStack>
                  </VStack>
                )}
                {/* 底部按钮 */}
                <HStack w="100%" justifyContent="center" gap="16px">
                  {showInfo?.modalBottomButtonText?.map((text, index) => {
                    if (typeof text === 'function') {
                      return (
                        <div
                          key={index}
                          onClick={() => {
                            onClose(false)
                            if (showInfo?.modalBottomButtonClick) {
                              showInfo.modalBottomButtonClick(index)
                            }
                          }}
                        >
                          {text()}
                        </div>
                      )
                    }

                    return (
                      <Button
                        key={index}
                        variant="outline"
                        onClick={() => {
                          onClose(false)
                          if (showInfo?.modalBottomButtonClick) {
                            showInfo.modalBottomButtonClick(index)
                          }
                        }}
                      >
                        {text}
                      </Button>
                    )
                  })}
                </HStack>
              </VStack>
            }
          </VStack>
        </ModalBody>
      </ModalContent>
    </Modal>
  )
}
