import useTokenSelect from '@/hook/useTokenSelect'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import useTokenStore from '@cetus/stores/src/token'
import useTokenSelectStore from '@cetus/stores/src/useTokenSelectStore'
import { Token } from '@cetus/types'
import { Icon, SingleCoinImage } from '@cetus/ui-kit'
import { Button, HStack, Input, InputGroup, InputRightElement, Menu, MenuButton, MenuList, Text, VStack } from '@chakra-ui/react'
import { cloneElement, ReactNode, useEffect, useState } from 'react'

interface SelectTokenListProps {
  value: Token
  onChange: (value: Token) => void
  children: ({ isOpen, onClose }: { isOpen: boolean; onClose: () => void }) => ReactNode | ReactNode
}

function SelectTokenList({ value, onChange, children }: SelectTokenListProps) {
  const [inputValue, setInputValue] = useState('')

  const { sortWithTokenList } = useTokenSelect()
  const { filterTokenList, filterTokenLoading, collectObj, importTokenList, isShowImportBtn } = useTokenSelectStore()
  const { verifiedTokenMap } = useTokenStore()
  const [list, setList] = useState<Array<Token>>(Array.from(verifiedTokenMap?.values()) as Array<Token>)
  const [tokenListLoading, setTokenListLoading] = useState(false)

  useEffect(() => {
    if (filterTokenLoading) {
      setList([])
    }
    if (inputValue) {
      setList(filterTokenList)
    } else {
      setList(sortWithTokenList([...Array.from(verifiedTokenMap?.values()), ...importTokenList], value?.coin_type))
    }
  }, [value, inputValue, filterTokenList, filterTokenLoading, importTokenList, verifiedTokenMap])

  const { isApp } = useWindowWidth()

  const onInputValueChange = (value: string) => {
    const regValue = value.replace(/[^[a-zA-Z0-9\s]+$/g, '')
    setInputValue(regValue)
    if (regValue) {
      console.log('🚀🚀🚀 ~ file: index.tsx:36 ~ changeInputValue ~ regValue:', regValue)
    }
  }

  return (
    <Menu isLazy>
      {({ isOpen, onClose }) => (
        <>
          <MenuButton isActive={isOpen} as={Button}>
            {typeof children === 'function'
              ? children({ isOpen, onClose })
              : cloneElement(children, {
                  isOpen,
                  onClose
                })}
          </MenuButton>
          <MenuList>
            <InputGroup>
              <Input placeholder="Search name or address" value={inputValue} onChange={e => onInputValueChange(e.target.value)} />
              <InputRightElement>
                {inputValue ? (
                  <Icon
                    xlinkHref="#icon-icon_close"
                    position="absolute"
                    top="8px"
                    right="8px"
                    onClick={e => {
                      e.stopPropagation()
                      setInputValue('')
                    }}
                  />
                ) : null}
              </InputRightElement>
            </InputGroup>
            {list?.map(token => <Item key={token?.coin_type} token={token} onChange={onChange} />)}
          </MenuList>
        </>
      )}
    </Menu>
  )
}

const Item = ({ token, onChange, isActive }: { token: Token; onChange: (value: Token) => void; isActive?: boolean }) => {
  return (
    <HStack
      w="100%"
      justify="flex-start"
      cursor="pointer"
      bg={isActive ? 'primary_opacity.10' : 'bg_secondary'}
      sx={{
        _hover: {
          bg: 'primary_opacity.10',
          borderRadius: '12px'
        }
      }}
      onClick={() => onChange(token)}
    >
      <SingleCoinImage imageUrl={token.logo_url} w="34px" h="34px" />
      <VStack>
        <Text color="text_caption">{token?.symbol}</Text>
        <Text>{token?.name}</Text>
      </VStack>
    </HStack>
  )
}

export default SelectTokenList
